* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
html {
  scroll-behavior:smooth
}
body {
  background: #e9e9e9;
  color: #4b5a6a;
  font-family: 'Open Sans', sans-serif;
}
.container {
  max-width: 85%;
  padding: 0;
  margin: 0 auto;
}
.row {
  display: flex;
  flex-wrap: wrap;
}
.col-2 {
  width: 50%;
}
.col-3 {
  width: 33.333333%;
}
.col-4 {
  width: 25%;
}
img {
  max-width: 100%;
  height: auto;
}
header {
  position: absolute;
  width: 100%;
  background-color: transparent;
  padding: 20px 0;
  z-index: 2;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .menu-logo h2 a {
  text-decoration: none;
  color: #dd3751;
  font-size: 38px;
}
.header .menu ul {
  list-style-type: none;
  display: flex;
  font-size: 18px;
}
.header .menu ul li:not(:last-child) {
  margin-right: 20px;
}
.header .menu ul li a {
  text-decoration: none;
  color: #fff;
  font-size: 18px;
  transition: .3s all ease-in-out;
}
.header .menu ul li a:hover {
  color: #dd3751;
}
.home-hero {
  height: 100vh;
  background-size: cover;
  background-position: center;
  position: relative;
}
.home-hero::after,
.section-jumbotron::after {
  content: "";
  position: absolute;
  background-image: linear-gradient(50deg,rgba(24,39,52,0.8) 60%,rgba(24,39,52,0.5));
  background-color: transparent;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.content, .hero-content {
  position: relative;
  z-index: 1;
}
.hero-content {
  padding: 15rem 0 0;
  color: #fff;
  width: 65%;
}
.hero-content h1 {
  font-size: 54px;
}

/* ############### About Section ############### */

.about-section {
  padding: 80px 0;
}
.about-section h2 {
  font-size: 32px;
  font-weight: 600;
  color: #1c2936;
  margin-bottom: 40px;
}
.about-section span {
  font-size: 18px;
  font-weight: 600;
}
.about-section p {
  font-size: 18px;
  margin-top: 15px;
}
.about-section p,
.about-section span {
  padding-right: 50px;
  line-height: 24px;
}
.about-section .col-4 {
  margin-top: 40px;
}
.feature-box {
  padding: 20px;
  line-height: 24px;
  transition: all 0.5s ease-in-out;
}
.feature-box:hover  {
  background-color: #dd3751;
  color: #fff;
}
.feature-box:hover .icon {
  color: #fff;
}
.feature-box .icon {
  font-size: 30px;
  color: #dd3751;
  margin-bottom: 20px;
  transition: all 0.5s ease-in-out;
}
.feature-box .content h5 {
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
}
.feature-box p {
  padding: 0;
}

/* ############### Recent Cars ############### */

.recent-cars {
  background-color: #fff;
  padding: 80px 0;
}
.recent-cars h2 {
  font-size: 32px;
  font-weight: 600;
  color: #1c2936;
  margin-bottom: 40px;
  text-align: center;
}
.car-item {
  margin: 10px;
  border: 2px solid transparent;
  transition: all 0.5s ease-in-out;
  background-color: #e9e9e9;
  height: 100%;
}
.car-item:hover {
  border: 2px solid #dd3751;
}
.car-item .car-content {
  padding: 0px 20px;
}
.car-item .car-content h3 {
  margin-bottom: 15px;
}
.car-item .price-box {
  padding: 0px;
}
.recent-cars .link-wrapper {
  justify-content: center;
  margin-top: 50px;
}
.recent-cars .btn {
  text-decoration: none;
}

/* ############### About-2-Section ############### */

.about-2-section {
  padding: 80px 0;
  background-size: cover;
  position: relative;
  background-position: center;
}
.about-2-box {
  padding: 65px 80px;
  background-color: rgb(221 55 81 / 0.8);
  max-width: 800px;
  transition: .5s all ease-in-out;
}
.about-2-box:hover {
  background-color: rgb(221 55 81 / 1);
}
.about-2-box h2 {
  color: #1c2936;
  margin-bottom: 15px;
}
.about-2-box p {
  color: #fff;
  line-height: 24px;
}

/* ############### Jumbotron Section ############### */

.section-jumbotron {
  padding: 100px 0 50px;
  background: #606060;
  position: relative;
  color: #fff;
  background-size: cover;
  background-position: center;
}
.section-jumbotron .breadcrumbs {
  list-style-type: none;
  display: flex;
  font-size: 16px;
}
.section-jumbotron .breadcrumbs li a {
  color: #fff;
  text-decoration: none;
  transition: 0.3s all ease-in-out;
}
.section-jumbotron .breadcrumbs li:first-child {
  margin-right: 5px;
}
.section-jumbotron .breadcrumbs li:nth-child(2) {
  margin-left: 5px;
  color: #b6b6b6;
}
.section-jumbotron h2 {
  margin: 10px 0px;
  font-size: 42px;
  letter-spacing: 2.5px;
  color: #fff;
  font-weight: 600;
  text-transform: uppercase;
}

/* ############### Cars Section ############### */

.car-models {
  padding: 80px 0;
}
.link-wrapper {
  margin: 0 0 40px;
  display: flex;
  justify-content: flex-end;
}
.link-wrapper .add {
  text-decoration: none;
  border-color: #323232;
  color: #323232;
}
.link-wrapper .add:hover {
  color: #fff;
  background: #323232;
}
.car-box {
  display: flex;
  margin-bottom: 40px;
  background-color: #fff;
}
.car-box .car-img {
  width: 100%;
  flex: 0 0 50%;
  position: relative;
}
.car-box img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.car-box .car-img:after {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgb(221 55 81 / 0.7);
  transform: rotate(360deg);
  opacity: 0.3;
  transition: all 0.5s ease-in-out;
  visibility: hidden;
}
.car-box .car-img:hover:after {
    transition: all 0.5s ease-in-out;
    transform: rotate(180deg);
    visibility: visible;
}
.car-desc {
  min-height: 250px;
  flex: 0 0 50%;
  border: 1px solid #eaeaea;
  border-left: transparent;
  padding: 40px 25px 40px 35px;
  overflow: hidden;
}
.price-box {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.price-box .rated {
  float: left;
  margin: 0;
  display: block;
  text-transform: uppercase;
  color: #fff;
  font-size: 11px;
  font-weight: 500;
  background-color: #444444;
  padding: 7px 10px;
}
.price-box .price {
  color: #dd3751;
  font-size: 28px;
  margin-bottom: 15px;
  font-weight: 600;
}
.car-desc h3 {
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
  color: #444444;
  margin-bottom: 15px;
}
.car-desc p {
  font-size: 15px;
  line-height: 24px;
}
.car-info {
  display: flex;
  list-style-type: none;
  margin: 20px 0px;
}
.car-info li {
  display: flex;
  align-items: center;
  margin-right: 20px;
}
.car-info li p {
  margin-left: 5px;
  text-transform: capitalize;
}
.btn {
  border: 2px solid #dd3751;
  padding: 15px 25px;
  color: #dd3751;
  font-size: 16px;
  font-weight: 600;
  background: transparent;
  outline: none;
  transition: .4s all ease-in-out;
}
.btn:hover {
  color: #fff;
  background: #dd3751;
  cursor: pointer;
}
.btn svg {
  margin-left: 5px;
}

/* ############### AddCar/Form ############### */

.add-car {
  padding: 80px 0;
}
.form-wrapper {
  width: 100%;
  margin: 40px 0;
  padding: 40px;
  text-align: center;
  font-size: 30px;
  font-weight: 600;
  background: #fff;
  box-shadow: 3px 10px 20px 5px rgba(0, 0, 0, 0.5);
}
form {
  width: 60%;
  padding: 1rem;
  margin: 20px auto;

}
.form-group input, .form-group textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  outline: none;
  border: none;
  border-bottom: 2px solid #333;
  background-color: transparent;
  font-size: 16px;
  line-height: 20px;
}
form .btn {
  margin-top: 30px;
}
::placeholder, textarea::placeholder {
  font-size: 16px;
  text-transform: capitalize;
}
input:required:focus, textarea:required:focus {
  border-bottom: 1px solid red;
  outline: none;
}
input:required:valid {
  border-bottom: 1px solid green;
}

/* ############### Footer ############### */

.footer-section {
  background-color: #1c2936;
  padding: 80px 0 0;
  color: #fff;
}
.footer-section .row .col-2{
  padding-bottom: 50px;
}
.single-footer-widget {
  margin-right: 25px;
}
.single-footer-widget p {
  font-size: 16px;
  margin-bottom: 15px;
}
.footer-section h3 {
  font-size: 22px;
  margin: 10px 0;
}
.footer-section .logo {
  color:#dd3751;
  font-size: 30px;
}
.footer-section a {
  font-size: 20px;
  text-decoration: none;
  color: #dd3751;
}
.footer-contact ul {
  list-style-type: none;
}
.footer-contact ul li {
  color: #999999;
  font-size: 18px;
  display: flex;
  margin-bottom: 20px;
}
.footer-contact ul li a, .footer-contact ul li span {
  color: #fff;
  font-size: 18px;
  margin-left: 5px;
}
.copyright {
  padding: 20px 0px;
}

/* ############### Media Queries ############### */

@media only screen and (max-width: 800px) {
  .hero-content {
    width: 100%;
  }
  .hero-content h1 {
    font-size: 40px;
  }
  .col-2, .col-3, .col-4 {
    width: 100%;
    margin-bottom: 40px;
  }
  .about-section .col-4 {
    margin-top: 0px;
  }
  .recent-cars .link-wrapper {
    margin: 0px;
  }
  .car-box {
    flex-direction: column;
  }

  .footer-section .row .col-2 {
    padding-bottom: 0px;
  }
  form {
    width: 100%;
  }
  .about-2-section {
    padding: 40px 0px;
  }
  .about-2-box {
    padding: 45px 60px;
  }
}
